<template>
    <main class="app-main">
        <section class="top">
            <p class="title"><md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>基本信息</p>
        </section>
        <section class="bg-con"></section>
        <div class="con-con">
            <div class="head-portrait"><img :src="userInfo.img" alt=""></div>
            <div class="phone">{{ userImg }}<router-link to="/personal/entEditInfo"><img src="../../../assets/personal/13.png"/></router-link></div>
            <md-cell-item title="单位名称" :addon="userInfo.unitName | tagFilter"/>
            <md-cell-item title="单位统一社会代码" :addon="userInfo.creditCode | tagFilter"/>
            <md-cell-item title="单位法人" :addon="userInfo.legalPerson | tagFilter"/>
            <md-cell-item title="单位规模" :addon="userInfo.unitScale | tagFilter"/>
            <md-cell-item title="联系人" :addon="userInfo.name | tagFilter"/>
            <md-cell-item title="联系电话" :addon="userInfo.phone | tagFilter"/>
            <md-cell-item title="单位简介" :addon="userInfo.intro | tagFilter"/>
            <md-cell-item title="优势学科" :addon="userInfo.advantageDisciplines | tagFilter"/>
            <md-cell-item title="行业领域" :addon="userInfo.genre | tagFilter"/>
            <md-cell-item title="主营产品" :addon="userInfo.product | tagFilter"/>
        </div>
        <div class="bottom"></div>
    </main>
</template>

<script>
import { Field, CellItem, Icon, InputItem, Button } from 'mand-mobile'
import { getInfo } from '../../../apiUrl/personal/info'
export default {
  name: 'index',
  components: {
    [Field.name]: Field,
    [CellItem.name]: CellItem,
    [Icon.name]: Icon,
    [Button.name]: Button,
    [InputItem.name]: InputItem
  },
  data () {
    return {
      userInfo: {
        academic: '',
        education: '',
        email: '',
        genre: '',
        graduationSchool: '',
        idCard: '',
        img: '',
        intro: '',
        mobile: '',
        name: '',
        post: '',
        roleId: 1,
        status: '',
        technicalTitle: '',
        type: '',
        unit: '',
        userId: ''
      },
      userImg: this.$store.state.user.userInfo.phone
    }
  },
  mounted () {
    this.getInfo()
  },
  methods: {
    getInfo () {
      getInfo().then((res) => {
        this.userInfo = res.data
        if (!res.data.img) {
          this.userInfo.img = require('../../../assets/personal/item-touxiang@2x.png')
        }
      })
    },
    // 回退
    goBack () {
      this.$router.push('/personal')
    }
  }
}
</script>

<style lang="stylus" scoped>
    .top
        .title
            background url("../../../assets/personal/bg.png") no-repeat
    .bg-con
        width 100%
        height 200px
        margin-top 88px
        background url("../../../assets/personal/bg.png") no-repeat
    .con-con
        width 90%
        margin 0px auto
        margin-top -80px
        background:rgba(255,255,255,1);
        box-shadow:0px 4px 24px 0px rgba(2,25,85,0.1);
        border-radius:12px;
        padding-top 88px
        position relative
        .head-portrait
            position absolute
            left 50%
            margin-left -70px
            top -30px
            background rgba(2,25,85,0)
            border-radius 12px
            img
                width 140px
                height 140px
                margin-top -140px
        .phone
            font-size:28px;
            font-family:PingFang-SC-Medium;
            font-weight:500;
            color:rgba(2,25,85,1);
            line-height:36px;
            img
                width 25px
                height 28px
                margin-left 20px
    .bottom
        height 60px
    /deep/ .md-cell-item-right
        width 350px
    /deep/ .md-cell-item-content
        width 160px
        padding-left 30px
        margin-right 60px
    /deep/.md-cell-item-body:before
        top:91px
        bottom auto
    /deep/.md-button
        width:660px;
        height:88px;
        background:rgba(2,25,85,1);
        border-radius:12px;
        margin 0 auto
        color #fff
</style>
